import './index.scss';
import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import FullPage from 'layout/fullPage';
import SEO from 'components/seo';
import { GlobalMenus } from 'src/constants';

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "wallpaper.jpg" }) {
        childImageSharp {
          fluid (maxWidth: 1920, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  const mainJumboImage = `url(${data.image.childImageSharp.fluid.src})`

  return (
    <FullPage pageName="main">
      <SEO title="Home" />
      <section className="jumbotron" style={{ backgroundImage: mainJumboImage }}>
        <div className="dimmed"></div>
        <div className="wordbox">
          <p>Hello, World</p>
          <h2>This is <strong>Lubycon</strong></h2>
          <ul className="menus">
            {GlobalMenus.map((menu, index) => {
              return (
                <li key={index}>
                  <Link to={menu.link}>{menu.name}</Link>
                </li>
              )
            })}
          </ul>
        </div>
      </section>
    </FullPage>
  );
}

export default IndexPage
